@import './reset.css';

body {
    width: 100vw;
    height: 100vh;
    background: #3F72AF;
    background-size: cover;
    font-family: 'Lora', serif;
}

#root {
    width: 100%;
    height: 100%;
}

.input {
    font-size: 16px;
    width: 100%;
    background: #DBE2EF;
    border: 1px solid rgba(177, 177, 177, 0.4);
    border-radius: 5px;
    padding: 10px 10px;

    &:focus {
     outline: none;
     border-color: #112D4E;
 }
}

.btn {
    padding: 10px;
    font-size: 16px;
    background-color: #3F72AF;
    color: #F9F7F7;
    border: none;
    border-radius: 20px;
}
